import React from 'react';
import { Modal, Popover } from 'antd';
import { LoadingOutlined, CustomerServiceOutlined, RocketOutlined } from '@ant-design/icons';
import { FloatButton } from 'antd';

import { tokenDatos } from '../../../conf/token';
import axios from 'axios';
import './GeminiAsk.css';

// Función para convertir el JSON de Gemini a HTML
const convertGeminiJsonToHtml = (data) => {
    console.log('Gemini JSON Data:', data);
    if (!data || !data.message) {
        return '';
    }

    // Si message es string, parsearlo a JSON
    let messageData = data.message;
    if (typeof messageData === 'string') {
        try {
            messageData = JSON.parse(messageData);
        } catch (e) {
            console.error('Error parsing Gemini message:', e);
            return '';
        }
    }

    // Verificar que sea un array
    if (!Array.isArray(messageData)) {
        return '';
    }

    const renderElement = (item) => {
        if (!item) return '';

        const { element, content } = item;

        // Si content es un array (como en ol/ul), renderizar hijos
        if (Array.isArray(content)) {
            const children = content.map(child => renderElement(child)).join('');
            return `<${element}>${children}</${element}>`;
        }

        // Si content es string, renderizar directamente
        return `<${element}>${content}</${element}>`;
    };

    const renderSection = (sectionData, sectionTitle, icon) => {
        if (!sectionData || sectionData.length === 0) return '';

        const content = sectionData.map(item => renderElement(item)).join('');
        return `
            <div class="gemini-section">
                <div class="gemini-section-header">
                    <span class="gemini-section-icon">${icon}</span>
                    <span class="gemini-section-title">${sectionTitle}</span>
                </div>
                <div class="gemini-section-content">
                    ${content}
                </div>
            </div>
        `;
    };

    let html = '';

    messageData.forEach((item) => {
        // Título principal
        if (item.title && item.title.length > 0) {
            const titleContent = item.title.map(t => renderElement(t)).join('');
            html += `<div class="gemini-card">
                <div class="gemini-card-title">${titleContent}</div>`;
        }

        // Descripción
        if (item.description) {
            html += renderSection(item.description, 'Descripción', '📋');
        }

        // Objetivo
        if (item.objective) {
            html += renderSection(item.objective, 'Objetivo', '🎯');
        }

        // Metodología
        if (item.methodology) {
            html += renderSection(item.methodology, 'Plan de Acción', '🚀');
        }

        if (item.title && item.title.length > 0) {
            html += '</div>';
        }
    });

    return html;
};

function GeminiAsk() {

    const dataLogin = tokenDatos()
    const featureFlags = dataLogin?.feature_flags ?
        typeof dataLogin.feature_flags === 'string' ?
            JSON.parse(dataLogin.feature_flags)
            : dataLogin.feature_flags
        : {};
    const [geminiAdvice, setGeminiAdvice] = React.useState('')
    const [loading, setLoading] = React.useState(false)
    const [isVisible, setIsVisible] = React.useState(false)

    // Si la feature no está habilitada, no renderizar nada
    if (!featureFlags['gemini-ask']) {
        return null;
    }

    const hasEncuesta = dataLogin.ENCUESTANO && dataLogin.ENCUESTANO !== 0;

    const getGeminiAdvice = async () => {
        // Si no tiene encuesta, mostrar mensaje informativo
        if (!hasEncuesta) {
            const noEncuestaHtml = `
                <div class="gemini-card" style="text-align: center;">
                    <div style="font-size: 48px; margin-bottom: 16px;">📋</div>
                    <h3 class="gemini-card-title">Sin datos de encuesta</h3>
                    <p>
                        Aún no tienes ninguna encuesta asociada a tu cuenta.
                        Una vez que se complete una encuesta, podré generarte un plan de acción
                        personalizado basado en los resultados de tu equipo.
                    </p>
                </div>
            `;
            setGeminiAdvice(noEncuestaHtml);
            setTimeout(() => setIsVisible(true), 100);
            return;
        }
        setLoading(true);
        try {
            const query = await axios.post('https://empowerment4.com/api/gemini/survey', {
                company: Number(dataLogin.CODIGOEMPRESA),
                survey: Number(dataLogin.ENCUESTANO),
                codeemp: Number(dataLogin.CODEMP),
            })

            // Convertir el JSON a HTML
            const htmlContent = convertGeminiJsonToHtml(query.data);
            console.log('Gemini HTML Content:', htmlContent);
            setGeminiAdvice(htmlContent);
            setTimeout(() => setIsVisible(true), 100);
        } catch (error) {
            console.error('Error fetching Gemini advice:', error);
        } finally {
            setLoading(false);
        }
    }

    const handleClose = () => {
        setIsVisible(false);
        setTimeout(() => setGeminiAdvice(''), 300);
    }

    return (
        <>
            <FloatButton.Group
                trigger="hover"
                type="primary"
                style={{ insetInlineEnd: 94 }}
                icon={loading ? <LoadingOutlined spin /> : <CustomerServiceOutlined />}
            >
                <Popover
                    onClick={getGeminiAdvice}
                    placement="left"
                    title={<span className="gemini-popover-title">✨ Necesitas una sugerencia?</span>}
                    content={
                        <span className="gemini-popover-content">
                            Haz clic para obtener una sugerencia de plan de acción basada en los últimos resultados de tu equipo.
                        </span>
                    }
                >
                    <FloatButton
                        onClick={getGeminiAdvice}
                        icon={<RocketOutlined />}

                    />
                </Popover>
            </FloatButton.Group>

            <Modal
                width={1000}
                title={
                    <div className="gemini-modal-header">
                        <div className="gemini-avatar">
                            <span className="gemini-avatar-icon">🤖</span>
                        </div>
                        <div className="gemini-header-text">
                            <span className="gemini-greeting">Hola, {dataLogin.NOMBRE?.split(' ')[0] || "Usuario"}</span>
                            <span className="gemini-subtitle">Aquí está tu plan de acción personalizado</span>
                        </div>
                    </div>
                }
                open={geminiAdvice !== ''}
                onCancel={handleClose}
                footer={null}
                className="gemini-modal"
                centered
            >
                <div className={`gemini-content-wrapper ${isVisible ? 'visible' : ''}`}>
                    <div className="gemini-sparkle-bg"></div>
                    <div
                        className="gemini-response-content"
                        dangerouslySetInnerHTML={{ __html: geminiAdvice }}
                    />
                </div>
            </Modal>
        </>
    );
}

export default GeminiAsk;