import { gql } from "@apollo/client";

export const EMPRESASENC = gql`
  mutation empresaAd($input: inputID) {
    empresaallEnc(input: $input) {
      EMP
      ENCNO
      FECFIN
      FECHA
      FECIN
      ID
      STATUS
    }
  }
`;

