import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Idioma } from "../../../conf/Idioma";

const NavBarindex = (props) => {
  const [datosIdioma] = useState(Idioma());

  const cambioIdioma = () => {
    if (localStorage.getItem("idioma") === "ES") {
      localStorage.setItem("idioma", "EN");
    } else if (localStorage.getItem("idioma") === "EN") {
      localStorage.setItem("idioma", "ES");
    }

    window.location.reload();
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light bg-white">
      <div className="container-fluid">
        <a className="navbar-brand" href="/">
          <img
            src="https://static.wixstatic.com/media/b91e83_c517976cbada4e358c49d2b99bb2d7cc~mv2.png/v1/fill/w_299,h_75,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/b91e83_c517976cbada4e358c49d2b99bb2d7cc~mv2.png"
            alt="Logo"
            className="img-fluid"
          />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon "></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto">
            <li className="nav-item">
              <a className={props.pageindex} id="nav" href="#/">
              {datosIdioma.nav.inicio}
              </a>
            </li>
            <li className="nav-item">
              <a  className={props.pagesomos} id="nav" href="#/about">
              {datosIdioma.nav.quienes}
              </a>
            </li>
            <li className="nav-item">
              <a className={props.pagefuncional}
                id="nav" href="#/funcionalidades">
              {datosIdioma.nav.funcio}
              </a>
            </li>
            <li className="nav-item">
              <a className={props.pagecontacto} id="nav" href="#/contacto">
              {datosIdioma.nav.contacto}
              </a>
            </li>
            <li className="nav-item dropdown" id="nav">
              <div className="dropdown">
                <span>{localStorage.getItem('idioma')}</span>
                <div className="dropdown-content">
                  <button className="buttonEN" onClick={() => cambioIdioma() }>{(localStorage.getItem('idioma') === 'ES')?('EN'):('ES')}</button>
                </div>
              </div>
              {/* <a
                className="nav-link dropdown-toggle"
                href="/"
                id="languageDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                ES
              </a>
              <div className="dropdown-menu" aria-labelledby="languageDropdown">
                <a className="dropdown-item">ES</a>
                <a className="dropdown-item">US</a>
              </div> */}
            </li>
            <li className="nav-item">
              <a className="nav-link" id="nav" href="#/login">
              {datosIdioma.nav.login}
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default NavBarindex;
